import { Modal } from 'react-bootstrap';
import AwesomeSlider from 'react-awesome-slider';
import AwesomeSliderStyles from '../scss/light-slider.scss';

const ProjectDetailsModal = ({ show, project, onClose }) => {

  const { title, description, images, liveUrl, gitUrl, technologies } = project ?? {}

  return (
    <Modal
      className='project-modal'
      onHide={onClose}
      show={show}
      size='lg'
      aria-labelledby='contained-modal-title-vcenter'
      centered
    >
      <span onClick={onClose} className='modal-close'>
        <i className='fas fa-times fa-3x close-icon'></i>
      </span>
      <div className='col-md-12'>
        <div className='col-md-10 mx-auto' style={{ paddingBottom: '50px' }}>
          <div className='slider-tab'>
            <span
              className='iconify slider-iconfiy'
              data-icon='emojione:red-circle'
              data-inline='false'
              style={{ marginLeft: '5px' }}
            ></span>{' '}
            &nbsp;{' '}
            <span
              className='iconify slider-iconfiy'
              data-icon='twemoji:yellow-circle'
              data-inline='false'
            ></span>{' '}
            &nbsp;{' '}
            <span
              className='iconify slider-iconfiy'
              data-icon='twemoji:green-circle'
              data-inline='false'
            ></span>
          </div>
          <AwesomeSlider
            cssModule={[AwesomeSliderStyles]}
            animation='scaleOutAnimation'
            className='slider-image'
          >
            {images && images.map((imgPath, index) => (
              <div key={index} data-src={imgPath} />
            ))}
          </AwesomeSlider>
        </div>
        <div className='col-md-10 mx-auto'>
          <h3 style={{ padding: '5px 5px 0 5px' }}>
            {title}
            {liveUrl ? (
              <a
                href={liveUrl}
                target='_blank'
                rel='noopener noreferrer'
                className='link-href'
              >
                <i
                  className='fas fa-external-link-alt'
                  style={{ marginLeft: '10px' }}
                ></i>
              </a>
            ) : null}
            {gitUrl ? (
              <a
                href={gitUrl}
                target='_blank'
                rel='noopener noreferrer'
                className='link-href'
              >
                <i
                  className='fab fa-github'
                  style={{ marginLeft: '10px' }}
                ></i>
              </a>
            ) : null}
          </h3>
          <p className='modal-description'>{description}</p>
          <div className='col-md-12 text-center'>
            <ul className='list-inline mx-auto'>
              {technologies && technologies.map((tech, index) => (
                <li className='list-inline-item mx-3' key={index}>
                  <span>
                    <div className='text-center'>
                      <i className={tech.class} style={{ fontSize: '300%' }}>
                        <p className='text-center' style={{ fontSize: '30%' }}>
                          {tech.name}
                        </p>
                      </i>
                    </div>
                  </span>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    </Modal>
  )
}
export default ProjectDetailsModal